.overlay {
  z-index: 999;
  background: rgba(0, 0, 0, 0.363);
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup_background {
  background: #ffd3b6;
  padding: 10px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-top: 50px;
  position: relative;
}
.popup_nav {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 0 20px;
  flex-direction: column;
  gap: 15px;
}
.popup_nav h3 {
  padding: 0;
  margin: 0;
  text-align: left;
  width: 100%;
  display: flex;
  align-items: center;
}
.popup_nav_icon {
  position: absolute;
  right: 10px;
  top: 10px;
  background-color: #e72a41;
  color: #fff;
  border-radius: 50%;
  padding: 5px;
  cursor: pointer;
  font-size: 1rem !important;
}
.popup_body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
}
.popup_submit_btn {
  border: none;
  margin: 20px;
  letter-spacing: 0.7px;
  width: max-content;
  font-weight: 600;
  text-transform: uppercase;
  padding: 10px 20px;
  color: #fff;
  background: #e72a41;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
    rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.popup_input_lable {
  font-size: 15px;
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.popup_input_lable input {
  font-size: 20px;
  padding: 5px;
}
.price_templetes_container {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr 1fr;
}
.price_templetes {
  padding: 10px;
  margin: 10px;
  background-color: var(--main);
  color: #fff;
  border-radius: 1rem;
  cursor: pointer;
  border: 1px solid var(--main);
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
    rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.price_templetes_active {
  background: #fff;
  color: var(--main);
}
.report_columns {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  width: 300px;
}
.report_container {
  /* border: 1.5px solid rgba(0, 0, 0, 0.7) ; */
  padding: 10px;
  background-color: #e72a41;
  border-radius: 0.8rem;
  display: flex;
  flex-direction: column;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}
.report_container img {
  width: 110px;
  height: 180px;
  object-fit: fill;
  padding-bottom: 10px;
  border-top-left-radius: 0.8rem;
  border-top-right-radius: 0.8rem;
}
.report_container h5 {
  margin: 0;
  color:white !important;
  letter-spacing: 1px;
  text-align: left;
  font-weight: 400;
  font-size: 12px;
}
.report_container_active {
  border: 2px solid var(--main);
  transform: scale(1.1);
}
.report_container_active span {
  color: var(--main);
}
.user_report_table {
  width: 100%;
}

.user_report_table th {
  border: 1px solid var(--main);
}
.user_report_table_unit {
  border: 1px solid var(--main);
  border-top: none;

  padding: 10px 15px;
}
.user-table {
  border-collapse: collapse;
  table-layout: fixed;
  text-align: left;
}

.user-table tr {
  text-align: left;
  border-bottom: 1px solid #d0cece;
  color: #4f4f4f;
  cursor: pointer;
  font-size: 10px;
}
/* .user-table .tbody  tr:hover{
background: rgba(0, 0, 0, 0.175);
} */

.user-table thead {
  background: #e72a41;
  padding: 5px;
}

.user-table thead th {
  padding: 5px;
  display: table-cell;
  vertical-align: inherit;
  color: white;
  font-weight: 500;
  border-right: 1px solid white;
}

.user-table tbody {
  text-align: center;
}

.user-table tbody td {
  background-color: #ffd3b6;
  color: #e72a41;
  overflow: hidden;
  padding: 5px;
  text-overflow: ellipsis;
  font-size: 10px;
}
.searchInput {
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  font-size: small;
  max-width: 32rem;
  width: 7.8rem;
  margin-top: 4px;
}
.select,
.multiSelect,
.numberInput {
  padding: 0.2rem;
  font-size: medium;
}

.select,
.numberInput {
  height: 2.4rem;
}

.select,
.multiSelect,
.numberInput,
.messageTextarea {
  outline-color: var(--mainColor);
}
.select,
.multiSelect,
.numberInput {
  padding: 0.2rem 1rem;
  border-radius: 5px;
  font-size: small;
  max-width: 32rem;
  width: 8.8rem;
  margin-top: 4px;
}

.select,
.numberInput {
  height: 2.4rem;
  max-width: 32rem;
}

.select,
.multiSelect,
.numberInput,
.messageTextarea {
  outline-color: var(--mainColor);
}
.searchContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.playsView {
  border: none;
  color: var(--main);
  background: #fff;
  border: 2px solid var(--main);

  font-size: 15px;
  padding: 5px 10px;
  border-radius: 10px;
}
.playsViewActive {
  color: #fff;
  background: var(--main);
}
.balance-toast {
  display: flex;
  gap: 10px;
  justify-content: space-between;
  background-color: #e72a41;
  color: white;
  padding: 0.5rem;
  width: 100%;
}
input{
  font-weight: 600 !important;
  color:rgba(0, 0, 0, 0.7) !important;
  background-color: white !important;
  border-radius: 5px !important;
}
.inputContainer{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.table-container{
  display: flex;
  justify-content: flex-start;
  overflow: auto;
  flex: 1;
}
.page {
  width: 100vw;
  height: 100vh;
  text-align: center;
  position: fixed;
  background: #fff;

}
.equal{
  left: 0;
  top: 0;
  z-index: 99;
  color: rgb(247, 121, 121);
  background: #000;
  
}
.smaller{

  z-index: 9;
  color: #000;
  background: rgb(239, 134, 134);
  visibility: hidden;

}
.greater{

  color: #000;
  background: rgb(236, 129, 129);
  visibility: hidden;

}
html,
body {
    overscroll-behavior-y: contain;
}

.TestApp{
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(180deg,rgb(253, 148, 148),rgb(155, 239, 155));
}
.row{
  position: relative;
}
.container{
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: white;
  position: absolute;
  left: -50vw;
}
.container>img{
  width: 100%;
  height: 100%;
}