.main,
.buyCoin {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding-top: 1rem;
  gap: 1rem;
  justify-content: center;
  text-align: center;
  flex: 1;
}
.main_nav {
  display: flex;
  justify-content: center;
  width: 100%;
}
.main_nav_content {
  background: #fff;
  color: #000;
  width: 50%;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  padding: 10px 0;
  border-bottom: 2px solid #000;
  transition: all ease 1s;
}
.main_nav_content_active {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #e72a41;
  color: #fff;
  transition: all ease 1s;
}
.main_challege_btn {
  background: #fdbb58;
  color: white;
  width: fit-content;
  position: absolute;
  bottom: 10px;
  right: 15px;
}
.main_challege_btn_icon {
  cursor: pointer;
  background: var(--main);
  color: #fff;
  border-radius: 50%;
  padding: 20px;
  font-size: 2rem !important;
}
.main_body {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  gap: 10px;
  align-items: stretch;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 0.5rem;
  padding-bottom: 50px;
}
.main_challeges_box {
  width: 90%;
  border: 2px solid var(--main);
  padding: 10px;
  border-radius: 20px;
  margin: 10px 0;
  display: flex;
  align-items: center;
}
.main_challeges_box_user_profile {
  width: 50px;
  height: 50px;
  background-color: var(--main);
  color: #fff;
  display: flex;
  align-items: center;
  border-radius: 50%;
  padding: 20px;
}
.main_challeges_box_user {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.main_challeges_box_details {
  padding: 0 10px;
}
.main_challeges_box_details h1 {
  padding: 0;
  margin: 0;
}
.main_challeges_box_details button {
  border: none;
  color: #fff;
  background-color: var(--main);
  font-size: 20px;
  padding: 10px;
}
.main_room_code {
  width: 260px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.report_btn {
  color: red;
  position: absolute;
  right: "-100px";
  z-index: 99;
  font-size: larger;
  z-index: 9;
  top: 4px;
  right: 120px;
  cursor: pointer;
}
.dashboard_body {
  display: flex;
  padding: 30px;
  gap: 10px;
  justify-content: space-evenly;
  align-items: center;
}
.dashboard_challeges_box {
  width: 150px;
  height: 150px;
  /* border: 3px solid #e72a41 ; */
  border-radius: 20px;
  margin: 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all ease-in-out 350ms;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
    rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.dashboard_challeges_box:hover {
  transform: scale(1.1);
}
.dashboard_challeges_box h1 {
  margin: 0;
}
.notification {
  position: fixed;
  font-size: 16px;
  padding: 5px 10px;
  border-radius: 6px;
  background: #ffd3b6;
  color: #e72a41;
  transition: all ease-in 1s;
  bottom: -100px;
  left: calc(50vw - 100px);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,
    rgba(0, 0, 0, 0.22) 0px 10px 10px;
}
.notification_active {
  bottom: 100px;
}
.roomeCodeDiv {
  display: flex;
  gap: 0.5rem;
  justify-content: space-between;
  padding: 0.2rem 0.5rem;
  background: rgba(231, 42, 65, 0.57);
  height: fit-content;
  border-radius: 2px;
  cursor: pointer;
  min-width: 90px;
}
.setting_icons {
  position: absolute;
  top: 100px;
  right: 10px;
  cursor: pointer;
}
.buyCoin {
  border: 1px solid #fff;
  margin: 10px;
  width: 90%;
  justify-content: flex-start;
  padding-top: 0;
  height: fit-content;
}
.buyCoin_nav {
  color: #000;
  background: #fff;
  padding: 10px 5px;
}
.buyCoin_body {
  margin-top: 10px;
  color: #000;
}
.buyCoin_body .Inputs {
  background: #fff;
  margin: 10px 0;
}
.buyCoin_body .code {
  display: flex;
  align-items: center;
  flex-direction: row;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
.action_icons {
  cursor: pointer;
}
.action_icons:hover {
  scale: 2;
}
